<template>
   <div class="listViewTop">
     <img class="bg" :src="playlist.coverImgUrl" alt="">
        <div class="listViewTopNav">
            <div class="back">
              <svg class="icon" aria-hidden="true" @click="goback">
                <use xlink:href="#icon-left"></use>
              </svg>
              <div class="title">
                歌单
              </div>
            </div>
            <div class="right">
            <svg class="icon search" aria-hidden="true">
              <use xlink:href="#icon-sousuo"></use>
            </svg>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shezhi"></use>
            </svg>
          </div>

        </div>
      <!--内容-->
      <div class="content">
        <div class="content-left">
          <img :src="playlist.coverImgUrl" alt="">
          <span>{{ playCount(playlist.playCount) }}</span>
        </div>
        <div class="content-right">
          <h4>{{playlist.name}}</h4>
          <div class="userinfo">
            <img :src="creators.avatarUrl" alt="">
            <p>{{creators.nickname}}</p>
          </div>
          <p>{{playlist.description}}</p>
        </div>
      </div>
     <!--操作-->
     <ul class="operation">
       <li>
         <svg class="icon" aria-hidden="true">
         <use xlink:href="#icon-pinglun"></use>
       </svg>
         <span>{{ playlist.commentCount }}</span>
       </li>
       <li>
         <svg class="icon" aria-hidden="true">
           <use xlink:href="#icon-fenxiang"></use>
         </svg>
         <span>{{ playlist.shareCount }}</span>
       </li>
       <li>
         <svg class="icon" aria-hidden="true">
           <use xlink:href="#icon-yunxiazai"></use>
         </svg>
         <span>下载</span>
       </li>
       <li>
         <svg class="icon" aria-hidden="true">
           <use xlink:href="#icon-xuanze-duoxuan-tianchong"></use>
         </svg>
         <span>多选</span>
       </li>
     </ul>
   </div>
</template>

<script>
import {useRouter,useRoute} from 'vue-router'
import {reactive, toRefs} from 'vue';
export default {
  name: "Listview-top",
  props:{
    playlist:{
      type:Object
    },
    privileges:{
      type:Array
    }
  },
  setup(props){
    //实例化路由对象
    const route = useRoute();
    const router = useRouter();

    //返回上一页
    const goback = function (){
      router.go(-1);
    }

    //处理playlist.creator里的数据
    const creator =reactive({
      creators:props.playlist.creator
    });
    console.log('输出接收的数据playlist',props.playlist)
    console.log('输出接收的数据privileges',props.privileges)
    //处理播放数量的函数
    const playCount = function (data){
        let CountData = data/10000;
      console.log(data)
        return parseInt(CountData)+'w'
    }
  return{
      ...toRefs(creator),playCount,goback
   }
  }
}
</script>

