<!--音乐列表组件-->
<template>
    <div class="playlist">
        <div class="playlist-top">
          <div class="left">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-bofang"></use>
            </svg>
              <div class="title">
                播放全部
              <p>(共{{playlist.tracks.length}}首)</p>
            </div>
          </div>
          <div class="right">
              <div class="btn">
                + 收藏({{ ComputedNum(playlist.subscribedCount) }})
              </div>
          </div>
        </div>
        <div class="musiclist">
          <ul class="musiclist-ul">
            <li v-for="(item,index) in playlist.tracks" :key="item.id">
              <div class="musicname">
                <h4>{{index+1}}</h4>
                <div>
                  {{ item.name }}
                 <div class="anthor">
                   <span class="tag" v-for="(tag,index) in item.tns" :key="index">
                     {{tag}}
                   </span>
                   描述......
                 </div>
                </div>
              </div>
              <div class="caozuo">
                <div class="play">
                  <svg class="icon" aria-hidden="true" @click="play(index)">
                    <use xlink:href="#icon-bofang1"></use>
                  </svg>
                </div>
                <svg class="icon gengduo" aria-hidden="true">
                  <use xlink:href="#icon-androidgengduo"></use>
                </svg>
              </div>
            </li>
          </ul>
        </div>
    </div>
</template>

<script>
import {useStore} from 'vuex';
import {reactive,ref} from 'vue';
export default {
  name: "Play-list",
  props:{
    playlist:{
      type:Object,
    }
  },
  setup(props){
    //实例化sotre
    const store = useStore();

    //处理收藏数量过多的问题
    const ComputedNum = function (data){
        let res;
        if(data>10000){
           res = (data/10000)+'w';
        }else {
           res = data;
        }
        return res;
    }
    //把点击的歌单信息存入store里
    const play =function (data){
      console.log('歌单列表的数据',data);
      store.commit('setPlayIndex',data);
      //切换歌单后让按钮复位
      store.commit('setPlayStatus',false);
    }


    return {
      ComputedNum,play
    }
  }
}
</script>

<style scoped lang="less">
.playlist{
  background-color: #FFFFFF;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin-top: 0.2rem;
  .playlist-top{
    display: flex;
    justify-content: space-between;
    padding: 0 0.2rem;

    .left{
      display: flex;
      height: 0.7rem;
      align-items: center;
      .title{
        margin-left: 0.1rem;
      }
      p{
        display: inline;
        color: #cbc8c8;
      }
    }
    .right{
      margin-top: 0.1rem;
      .btn{
        background-color: #f04545;
        padding: 0.12rem 0.15rem;
        border-radius: 30px;
        color: #FFFFFF;
      }
    }
  }
  .musiclist{
    .musiclist-ul li{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 1.2rem;
      padding: 0 0.4rem;
      .musicname{
        display: flex;
        .anthor{
          margin-top: 0.1rem;
          font-size: 0.2rem;
          color: #868484;
          .tag{
            //padding: 0.01rem;
            border: 1px solid red;
            color: red;
            font-size: 0.2rem;
            border-radius: 2px;
          }
        }
        h4{
          width: 0.45rem;
          margin-right: 0.4rem;
          line-height: 0.8rem;
          text-align: center;
        }
      }
      .caozuo{
        display: flex;
        .play{
          margin-right: 0.2rem;
        }
      }
    }
  }
}

</style>