<!--歌单列表页面-->
<template>
    <div class="listMusic">
        <listview-top v-if="playlist.length!==0" :playlist="playlist" :privileges="privileges"></listview-top>
        <Playlist v-if="playlist.length!==0" :playlist="playlist"></Playlist>
    </div>
</template>

<script>
import {useStore} from 'vuex';
import ListviewTop from '@/components/Listview-top';
import Playlist from '@/components/Play-list';
import {onMounted, reactive, toRefs} from 'vue';
import {useRoute,useRouter} from 'vue-router';
import {getListIdMusic} from "@/network/getNewMusic";
export default {
  name: "ListView",
  components:{
    ListviewTop,Playlist
  },
  setup(){
    const store = useStore();
    const route = useRoute();
    const router = useRouter();
    //存储歌单列表
    const musiclist = reactive({
      playlist:[],
      privileges:[]
    })

    //发送请求的方法
    const getlist =async function (){
        let res = await getListIdMusic('/playlist/detail','get',{
          id:route.query.id
        });
        musiclist.playlist = res.data.playlist;
        musiclist.privileges = [...res.data.privileges];
        //把歌单提交到vuex
        store.commit('setPlaylist',res.data.playlist.tracks)
    }

    //组件创建前
    onMounted(()=>{
      getlist();
    });
    return{
    ...toRefs(musiclist)
    }
  }
}
</script>

